
import { defineComponent } from 'vue';
import Usuario from '@/Models/Usuario';
import LoginService from '@/Services/LoginService';

export default defineComponent({
    name: 'CadastroView',

    data(): {
        service: LoginService,
        usuario: Usuario,
        submitResult: string | undefined
    } {
        return {
            service: new LoginService(),
            usuario: new Usuario(),
            submitResult: undefined
        }
    },

    computed: {
        formValido() {
            return this.usuario.nome && this.usuario.nome.trim() != '';
        }
    },

    emits: ['goToPage'],

    methods: {

        goToPage(page: string) {
            this.$emit('goToPage', page);
        },

        limparErro() {
            if(this.submitResult)
                this.submitResult = undefined;
        },

        async cadastrarUsuario() {

            if (!this.usuario.nome || this.usuario.nome.trim() == '') {
                this.submitResult = 'Informe um nome de usurio.';
                return;
            }

            try {
                this.submitResult = undefined;
                await this.service.cadastrar(this.usuario.nome);
                this.usuario = new Usuario();
                this.submitResult = undefined;
                this.goToPage('Menu');
            }
            catch (error: any) {
                this.submitResult = error ?? 'Falha ao cadastrar usurio!';
            }
        }
    },

    async mounted() {
        if (!(await this.service.config())) {
            this.goToPage('Home');
        }
    },
});
